@use "global" as *;

.category-gallery {
  background: $bg;
  padding-bottom: rem(160);

  @include mq(sp) {
    padding-block: 0 rem(80);
  }
}

.category-gallery__inner {
  max-width: rem(1145);
  width: 100%;
  margin: 0 auto;
  padding-inline: rem(40);

  @include mq(sp-ss) {
    padding-inline: rem(20);
  }

  @include mq(sp-xs) {
    padding-inline: rem(10);
  }
}

.all-list-button--category {
  margin-top: rem(80);

  @include mq(sp) {
    margin-top: rem(40);
    margin-bottom: rem(80);
  }
}
